.header {
    background-color: rgb(2, 2, 58);
    height: 300px;
    color: #fff;
}
.header-text {
    font-size: 45px;
    font-weight: bolder;
    letter-spacing: 3px;
    padding: 5.1rem 0 0 28.1rem;
}
.form {
    width: 30rem;
    display: flex;
    justify-content: space-between;
    margin: 40px auto;
}

.todo-form {
    width: 80%;
    height: 50px;
    padding: 10px;
    border-radius: 6px;
}
.todo-form:focus {
    outline: none;
}

.todo-btn {
    width: 5rem;
    border-radius: 6px;
    border: 0;
    background-color:rgb(59, 61, 189);
    height: 48px;
}
