.todo-list {
    width: 90%;
    background-color: #fff;
    margin: 0 auto;
    padding: 15px;
    margin-bottom: 20px;
    border-radius: 10px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}
.input-text {
    background-color: white;
    width: 80%;
}
.delete-btn {
    float: right;
}

.container {
    background-color: rgb(238, 144, 20);
    width: 30rem;
    margin: -40px auto;
    border-radius: 10px;
    padding: 30px;
    margin-bottom: 5px;

}
.list-text {
    text-align: center;
    margin-bottom: 20px;
    font-size: 30px;
    font-weight: bolder;
}
.input-checkbox {
    margin-top: 2px;
}

.active {
    text-decoration: line-through;
    width: 80%;
    background-color: #fff;
    color: red
}